.sunburst {
  path {
    stroke: #fff;
  }

  .tooltip {
    position: absolute;
    top: 20px;
    left: 20px;

    padding: 3px;

    opacity: 0;
    color: #eee;
    border-radius: 5px;
    background-color: rgba(0, 0, 0, 0.618);

    font-size: 16px;
    font-weight: bold;
  }
}



